* {
  font-family: League Spartan, sans-serif;
  transition: background .3s;
}

body {
  min-height: 630px;
}

main {
  height: 100vh;
  min-height: 630px;
  place-content: center;
  display: grid;
}

.calculator-main {
  max-width: 490px;
  padding: 1rem;
}

.theme-menu {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.theme-menu h1 {
  font-size: 1.5rem;
}

.theme-controls {
  text-transform: uppercase;
  align-items: flex-end;
  gap: 1rem;
  font-size: .9rem;
  display: flex;
  position: relative;
}

.theme-controls span:nth-child(1) {
  margin-bottom: .32rem;
}

.theme-controls .theme-switcher-container {
  width: 70px;
  border-radius: 5rem;
  padding: 5px;
}

.theme-controls .theme-switcher-container .switch-theme-label {
  display: none;
}

.theme-controls .theme-switcher-container #hidden-range {
  width: 60px;
  cursor: pointer;
  opacity: 0;
  position: absolute;
}

.theme-controls .theme-switcher-container .theme__switcher-thumb {
  width: 15px;
  height: 15px;
  pointer-events: none;
  border-radius: 100%;
  transition: all .3s;
}

.theme-controls .theme-numbers {
  justify-content: space-between;
  padding: 0 .5rem;
  display: flex;
}

.display {
  border-radius: .6rem;
  margin: 1rem 0;
  padding: 1.5rem 1rem;
  display: flex;
}

.display .display-label {
  display: none;
}

.display input {
  text-align: right;
  width: 100%;
  font-size: 2rem;
  font-weight: 700;
}

.numpad {
  text-align: center;
  border-radius: .6rem;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.2rem;
  padding: 2rem;
  font-weight: 700;
  display: grid;
}

.number-btn {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border-radius: .6rem;
  justify-content: center;
  align-items: center;
  padding: .8rem 1.5rem;
  font-size: 2rem;
  transition: all .3s;
  display: flex;
}

.number-btn:hover {
  transform: translateY(2%);
}

.number-btn[data-type="action"] {
  text-transform: uppercase;
  font-size: 1.5rem;
}

.btn--reset {
  grid-column: 1 / 3;
}

.btn--result {
  grid-column: 3 / 5;
}

.attribution {
  width: 100%;
  text-align: center;
  padding: 10px;
  font-size: 11px;
  position: absolute;
  bottom: 0;
}

.attribution a {
  color: #3e52a3;
}

@media (max-width: 700px) {
  :root {
    font-size: 14px;
  }

  main {
    height: 100svh;
  }

  .calculator-main {
    margin: 0 auto;
    padding: 0;
  }

  .numpad {
    grid-template-columns: repeat(4, auto);
  }
}

